.content {

    &__col-main {
        border: 1px solid black;
        padding: 20px 5px;
        display: flex;

        flex-direction: column;

        h1 {
            font-weight: 600;
            font-size: 18px;
            text-align: center;
        }

        span {
            font-weight: 700;
            font-size: 18px;
            text-align: center;
        }
    }

    &__col {
        display: flex;
        width: 100%;
        margin: 0 0 10px 0;
        // align-items:;
    }

    &__row {
        display: flex;
        flex-direction: column;
    }

    &__input {
        width: 100%;
        height: 100%;
    }

    &__input input {
        width: 100%;
        border: 1px solid black;
        height: 100%;
        padding: 5px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;

    }

    &__submit {
        cursor: pointer;
        padding: 5px;
        width: 100%;
        border: 1px solid black;
        border-radius: 10px;
    }

    &__input:last-child {
        margin: 0;
    }

    &__select {
        height: 100%;
        
    }

    &__select select {
        height: 100%;
        background: grey;
        padding: 7px;
        border: 0px transparent;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
    }

    &__select option {
        background: lightcyan;
    }
}